	const app = getApp()
export default {
	data() {
		return {
			object: [],
			index: 0,
			classIndex: 0,
			num: 1,
			minusStatus: 'disabled',
			classify: [],
			price: '',
			countPrice: ''
		};
	},
	onLoad() {
		this.getObject()
		this.submintOrder= this.$helper.debounce(this.confirmPay, 1500)
	},
	methods: {
		bindPickerChange(e) {
			console.log(e.currentTarget.dataset.id)
			this.index = e.target.value;
			this.classify_id = e.currentTarget.dataset.id
			this.price = this.object[this.index].curriculum[this.classIndex].price
			this.countPrice = this.object[this.index].curriculum[this.classIndex].price
			this.id =  this.object[this.index].curriculum[this.classIndex].id
			this.num = 1
			// this.getClassify()
		},
		bindChange(e){
			console.log(e)
			this.classIndex = e.target.value;
			this.price = this.object[this.index].curriculum[this.classIndex].price
			this.countPrice = this.object[this.index].curriculum[this.classIndex].price
			this.id =  this.object[this.index].curriculum[this.classIndex].id
			this.num = 1
		},
		getObject(){
			// uni.showToast({
			// 	title: "加载中...",
			// 	icon: 'loading'
			// });
			uni.request({
				url: `${this.$helper.requestUrl}pay/classify`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading()
					res = this.$helper.null2str(res.data);
					console.log(res);
					if (res.status_code == 'ok') {
						this.object = res.data
						this.price =  this.object[0].curriculum[0].price
						this.countPrice = this.object[0].curriculum[0].price
						this.id = this.object[0].curriculum[0].id
						console.log(this.id)
						this.classify_id =res.data[0].id
						// this.getClassify()
					} else {

					}
				}
			});
		},
		getClassify(){
			uni.request({
				url: `${this.$helper.requestUrl}pay/curricula`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data:{
					classify_id: this.classify_id
				},
				success: res => {
					res = this.$helper.null2str(res.data);
					console.log(res);
					if (res.status_code == 'ok') {
						this.classify = res.data
						this.price =  this.classify[0].price
						this.countPrice = this.classify[0].price
						this.id = this.classify[0].id
					} else {
			
					}
				}
			});
		},
		/* 点击减号 */

		bindMinus() {
			var num = this.num;
			// 如果大于1时，才可以减
			if (num > 1) {
				num--;
			}
			// 只有大于一件的时候，才能normal状态，否则disable状态
			var minusStatus = num <= 1 ? 'disabled' : 'normal';
			// 将数值与状态写回
			this.num = num
			this.countPrice = (num * this.price).toFixed(2)
			// console.log(parseFloat(num * this.price))
			this.minusStatus =minusStatus
		},
		/* 点击加号 */

		bindPlus() {
			var num = this.num;
			// 不作过多考虑自增1
			num++;
			// 只有大于一件的时候，才能normal状态，否则disable状态
			var minusStatus = num < 1 ? 'disabled' : 'normal';
			// 将数值与状态写回
			this.num = num
			// this.countPrice = num * this.price
			this.countPrice = (num * this.price).toFixed(2)
			// this.countPrice = num * this.priceOrder
			this.minusStatus =minusStatus
		},
		/* 输入框事件 */
		bindManual(e) {
			var num = e.detail.value;
			num.replace(/\s+/g, '')
			console.log(num)
			let reg = /^[1-9]\d*$/
			if(!reg.test(num)){
				num = '1'
			}
			this.num = num
			console.log(this.num,'*')
		},
		confirmPay(){
			uni.request({
				url: `${this.$helper.requestUrl}pay/exchangeCodePlaceOrder`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					classify_id: this.classify_id,
					curricula_id: this.id,
					num: this.num,
				},
				success: res => {
					res = this.$helper.null2str(res.data);
					console.log(res, '下单信息');
					// uni.hideToast()
					if (res.status_code == 'ok') {
						this.goBuy(res.data)
			
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
				}
			});
		},
		goBuy(orderId){
			uni.request({
				url: `${this.$helper.requestUrl}pay/pay`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					type: 'exchangeCode',
					ordernum: orderId,
					// discover_id: this.findId
				},
				success: res => {
					res = this.$helper.null2str(res.data);
					console.log(res, '-------------------');
					uni.hideToast()
					if (res.status_code == 'ok') {
						// this.goBuy(res.data)
						this.getPay(res.data);
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
				}
			});
		},
		getPay(payNode) {
			uni.showToast({
				title: "支付加载中...",
				icon: 'loading',
				duration: 2000
			});
			uni.requestPayment({
				provider: 'wxpay',
				timeStamp: payNode.timeStamp,
				nonceStr: payNode.nonceStr,
				package: payNode.package,
				signType: payNode.signType,
				paySign: payNode.paySign,
				success: res => {
					uni.hideToast()
					res = this.$helper.null2str(res)
					console.log(res)
					// console.log('success:' + JSON.stringify(res));
					uni.showToast({
						title: "支付成功",
						icon: 'success',
						duration: 2000
					});
					// 两秒后返回上一页
					setTimeout(e => {
						uni.navigateBack({
							data: 1
						})
					}, 2000)
				},
				fail: err => {
					uni.showToast({
						title: "支付失败",
						icon: 'none',
						duration: 2000
					});
				}
			});
		},
		
		
	}
};
